import React from 'react';
import Layout from '../components/layout';
import { useStaticQuery, graphql } from "gatsby";
import styled from 'styled-components';

const AboutCon = styled.div`

`
const AboutTitle = styled.h1``

const AboutSubText = styled.p``

const IngredientTitle = styled.div``
const Aboutya = () => {

  const data = useStaticQuery(graphql`
  {
    sanityAboutya {
      aboutTitle
      subTitle {
        children {
          text
        }
      }
      cardArray {
        ingredientPic {
          altText
          image {
            asset {
              gatsbyImageData
            }
          }
        }
        ingredientTitle
        ingredientText {
          children {
            text
          }
        }
      }
    }
  }
`)
  return (
    <Layout>
      <AboutCon>
<AboutTitle>{data.sanityAboutya.aboutTitle}</AboutTitle>
{data.sanityAboutya.subTitle.map(ST =>{
  return(
    ST.children.map(payload =>{
      return(
       <AboutSubText> {payload.text}</AboutSubText>
      )
    })
  )
})}
 <IngredientTitle> {data.sanityAboutyacardArray.ingredientTitle}</IngredientTitle> 
      </AboutCon>
    </Layout>
  )
}

export default Aboutya